<script>
import axios from 'axios'
import moment from 'moment'
import DonutChart from '../../../components/Graficos/DonutChart.vue'
export default {
  name: 'Home',
  components: { DonutChart },
  data: vm => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    loader: false,
    loadingQtdRh: true,
    qtdObrasAtivas: 0,
    qtdObrasPausadas: 0,
    qtdFrentesAtivas: 0,
    qtdFrentesPausadas: 0,
    qtdColaboradoresAtivas: 0,
    qtdColaboradoresPausadas: 0,

    // Filtros
    menu1: false,
    dataHoje: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dataPesquisa: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    filterObra: [],
    arrayObra: [],
    filterFrenteTrabalho: [],
    arrayFrenteTrabalho: [],
    filterColaboradores: [],
    arrayColaboradores: [],

    // Grficos
    loadingDashRH: false,
    componentKey: 0
  }),

  created () {
    console.log(window.location)
    this.getPesquisa()
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.dataPesquisa)
    }
  },
  watch: {
    dataPesquisa (val) {
      this.dateFormatted = this.formatDate(this.dataPesquisa)
    }
  },
  methods: {
    formatDate (data) {
      if (!data) return null

      const [year, month, day] = data.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (data) {
      if (!data) return null

      const [day, month, year] = data.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    async getDashRH () {
      this.loadingQtdRh = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}get-dash-rh`
      }
      axios(options)
        .then(res => {
          console.log(res.data)

          // Obras
          const qtdObrasTotal = res.data.obras.length
          this.qtdObrasAtivas = res.data.obras.filter(x => x.ativo).length
          this.qtdObrasPausadas = qtdObrasTotal - this.qtdObrasAtivas
          this.arrayObra = res.data.obras.map(x => { return x.nome })

          // Frente Trabalho
          const qtdFrenteTotal = res.data.frenteTrabalho.length
          this.qtdFrentesPausadas = res.data.frenteTrabalho.filter(x => x.inativo).length
          this.qtdFrentesAtivas = qtdFrenteTotal - this.qtdFrentesPausadas
          this.arrayFrenteTrabalho = res.data.frenteTrabalho.map(y => { return y.nome })

          // Colaboradores
          const qtdColaboradores = res.data.colaboradores.length
          this.qtdColaboradoresPausadas = res.data.colaboradores.filter(x => x.status === 'inativo').length
          this.qtdColaboradoresAtivas = qtdColaboradores - this.qtdColaboradoresPausadas
          this.arrayColaboradores = res.data.colaboradores.map(w => { return w.matricula + ' - ' + w.nome + ' - ' + w.funcao })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao buscar Dados!!!'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingQtdRh = false
        })
    },

    getQtdPresenca () {
      this.loadingDashRH = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}qtd-presencas-dash`,
        data: {
          dataInicio: moment(this.dataPesquisa).locale('pt-br').set({ hour: 0, minute: 0, second: 0, millisecond: 0 }).valueOf(),
          dataFim: moment(this.dataPesquisa).locale('pt-br').set({ hour: 23, minute: 59, second: 59, millisecond: 0 }).valueOf(),
          obras: this.filterObra.length > 0 ? this.filterObra : []
        }
      }
      axios(options)
        .then(res => {
          const presencas = res.data.parasQtdPresenca
          const paramsPresenca = [
            { dados: 'No Iniciado', valor: presencas.qtdNaoIniciado },
            { dados: 'Finalizado', valor: presencas.qtdFinalizado },
            { dados: 'Em Andamento', valor: presencas.qtdEmAndamento }
          ]
          this.statusPresenca = paramsPresenca
          this.colorPresenca = ['#F44336', '#4CAF50', '#FF9800']
          this.componentKey += 1
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao Buscar Qtd Presenas!!!'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingDashRH = false
        })
    },

    async getPesquisa () {
      await this.getDashRH()
      this.getQtdPresenca()
    }

    /* async getProjetosAtivos () {
      const result = await axios({
        method: 'GET',
        url: `${this.urlProd}dominio/projetos-clientes`
      })
      const resultFilter = result.data.filter(item => item.projeto.ativo === true && item.projeto.pausado === false)
      this.qtdProjetosAtivos = resultFilter.length
    } */
  }
}
</script>

