<template>
  <div class="example">
    <apexchart width="100%" height="150%" type="donut" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>

<script>
export default {
  name: 'DonutChart',
  props: ['title', 'dados', 'color'],
  async created () {
    await this.updateChart()
  },
  data: function () {
    return {
      series: [],
      chartOptions: {
        chart: {
          type: 'donut'
        },
        plotOptions: {
          pie: {
            customScale: 1,
            donut: {
              labels: {
                show: true,
                total: {
                  showAlways: false,
                  show: true
                }
              }
            }
          }
        },
        labels: [],
        dataLabels: {
          dropShadow: {
            blur: 3,
            opacity: 0.8
          }
        },
        title: {
          text: '',
          align: 'center',
          style: {
            fontSize: '18px',
            color: '#616161',
            fontFamily: 'Poppins'
          }
        },
        colors: [],
        legend: {
          position: 'bottom'
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom'
            }
          }
        }]
      }
    }
  },
  methods: {
    async updateChart () {
      const newData = this.dados.map(dado => { return dado.valor })
      const newCategories = this.dados.map(dado => { return dado.dados })
      this.series = newData
      this.chartOptions.labels = newCategories
      this.chartOptions.colors = this.color
      this.chartOptions.title.text = this.title
    }
  }
}
</script>
